    }

</script>
<style>
    .left-list {
        position: relative;
        float: left;
        width: 260px;
        background-color: #fff;
        margin-bottom: -2000px;
        padding-bottom: 2000px;
        min-height: 200px;
        margin-top: 20px;
        max-height: 500px;
        overflow: auto;
        border-right: solid 1px #e6e6e6;
        text-align: left;
    }

    .left-list:after {
        clear: both;
    }

    #project-list .el-menu {
        border: none;
        margin-top: 8px;
    }

    #project-list .el-menu-item:first-child {
        margin-left: 10px;
    }

    #project-list .el-input.search-input {
        width: 200px;
        margin-left: 35px;
    }

    #project-list .project-add {
        margin-top: 20px;
        margin-left: 35px;
    }

    #project-list .list-item {
        text-align: left;
        cursor: auto;
    }

    #project-list .list-item.is-active {
        color: #303133;
        background-color: #def1ff;
    }

    #project-list .list-item.is-active:hover {
        background-color: #ecf5ff;
    }

    #project-list .list-item:hover {
        background-color: #ecf5ff;
    }

    #project-list .list-item:last-child {
        margin-right: 10px;
    }

    #project-list .el-menu-item {
        height: 33px;
        line-height: 33px;
    }

    .el-date-editor.el-input.el-input--small.el-input--prefix.el-input--suffix.el-date-editor--year {
        width: 150px;
    }

    ul.el-upload-list.el-upload-list--text {
        position: absolute;
        top: -6px;
        left: 150px;
    }

    .home-pages {
        margin-right: 22px;
    }

    .item-name {
        color: #6f7180;
        width: 130px;
        display: inline-block;
        overflow: hidden;
        text-overflow: ellipsis;
    }

</style>


